/** name hookS     .
 * , Hooks State  name, setName   . */
const GroupingState = (name, hookS, setHookS) => {

  const nameSet = 'set' + (name.charAt(0).toUpperCase() + name.slice(1));

  const objData = new Object();

  objData[`${name}`] = hookS;
  objData[`${nameSet}`] = setHookS;

  return objData;
};

export default GroupingState;
