import axios from 'axios';
import TokenVerification from 'lib/Token/TokenVerification';
import { SERVER } from 'config/config.json';

class petitionRepository {
  async getPetitionFeed (page, limit, type) { //      
    try {
      const { data } = await axios.get(`${SERVER}/petition?page=${page}&limit=${limit}&type=${type}`);
      return data;
    } catch (error) {
      throw error;
    }
  }

  async getPetitionFeedByCategory (page, limit, category) {//     
    try {
      const { data } = await axios.get(`${SERVER}/petition/category/?page=${page}&limit=${limit}&category=${category}`);
      return data;
    } catch (error) {
      throw error;
    }
  }

  async searchPetition (title, page, limit) {//     
    try {
      const { data } = await axios.get(`${SERVER}/petition/search?title=${title}&page=${page}&limit=${limit}`);
      return data;
    } catch (error) {
      throw error;
    }
  }

  async writePetitionComment (request) {//     
    const token = TokenVerification() === 'localT' ? localStorage.getItem('petition-token') : sessionStorage.getItem('petition-token'); 
    try {
      const { data } = await axios.post(`${SERVER}/petition/comment`, request, {
        headers: {
          'x-access-token' : token,
        }
      });
      return data;
    } catch (error) {
      throw error;
    }
  }

  async getPetitionDetail (idx) {//       
    try {
      const { data } = await axios.get(`${SERVER}/petition/detail?idx=${idx}`);
      return data;
    } catch (error) {
      throw error;
    }
  }

  async getPetitionComments (idx) {//        
    try {
      const { data } = await axios.get(`${SERVER}/petition/comment?petitionIdx=${idx}`);
      return data;
    } catch (error) {
      throw error;
    }
  }

  async deletePetition (idx) {//     
    const token = TokenVerification() === 'localT' ? localStorage.getItem('petition-token') : sessionStorage.getItem('petition-token'); 

    try {
      const { data } = await axios.delete(`${SERVER}/petition?idx=${idx}`, {
        headers: {
          'x-access-token' : token,
        }
      });
      return data;
    } catch (error) {
      throw error;
    }
  }

  async answerToPetition (request) {//     
    const token = TokenVerification() === 'localT' ? localStorage.getItem('petition-token') : sessionStorage.getItem('petition-token'); 

    try {
      const { data } = await axios.post(`${SERVER}/petition/answer`, request, {
        headers: {
          'x-access-token' : token,
        }
      });
      return data;
    } catch (error) {
      throw error;
    }
  }

  async getStudentCouncilPetition (page, limit, type) {//       
    const token = TokenVerification() === 'localT' ? localStorage.getItem('petition-token') : sessionStorage.getItem('petition-token'); 

    try {
      const { data } = await axios.get(`${SERVER}/petition/get_student_council?page=${page}&limit=${limit}&type=${type}`, {
        headers: {
          'x-access-token' : token,
        }
      });
      return data;
    } catch (error) {
      throw error;
    }
  }

  async blindPetition (request) {//     
    const token = TokenVerification() === 'localT' ? localStorage.getItem('petition-token') : sessionStorage.getItem('petition-token'); 

    try {
      const { data } = await axios.post(`${SERVER}/petition/blind`, request, {
        headers: {
          'x-access-token' : token,
        }
      });
      return data;
    } catch (error) {
      throw error;
    }
  }

  async writePetition (request) { //    
    //  
    const token = TokenVerification() === 'localT' ? localStorage.getItem('petition-token') : sessionStorage.getItem('petition-token'); 

    try {
      const { data } = await axios.post(`${SERVER}/petition/`, request, {
        headers: {
          'x-access-token' : token,
        },
      });
      return data;
    } catch (error) {
      throw error;
    }
  }

  async updatePetitionAnswer (request) { //     
    //  
    const token = TokenVerification() === 'localT' ? localStorage.getItem('petition-token') : sessionStorage.getItem('petition-token'); 

    try {
      const { data } = await axios.put(`${SERVER}/petition/answer`, request, {
        headers: {
          'x-access-token' : token,
        },
      });
      return data;
    } catch (error) {
      throw error;
    }
  }
}

export default new petitionRepository();
