import { useState } from 'react';
import { action, observable } from 'mobx';
import { autobind } from 'core-decorators';
import petitionRepository from './petitionRepository';

@autobind
class petitionStore {
  @observable categoryPetitions = []; //   
  @observable categoryPetitionPageIndex = 1; //    index
  @observable categoryPetitionTotalPage = 1; //    
  @observable allowedPetitions = []; //   
  @observable allowedPetitionTotalPage = 1; //    
  @observable allowedPetitionPageIndex = 1; //    index

  @observable studentCouncilPetitions = []; //     
  @observable studentCouncilPetitionsTotalpage = 1;
  @observable studentCouncilPetitionsPageIndex = 1;

  @observable petitionComment = []; //  

  @observable PetitionDetailData = {}; //    

  @action
  handleAllowedPage(pageIndex) { // item index   handleStudentCouncilPage
    this.allowedPetitionPageIndex = pageIndex;
  }

  @action
  handleStudentCouncilPage(pageIndex) { // item index  
    this.studentCouncilPetitionsPageIndex = pageIndex;
  }

  @action
  handleAllPage(pageIndex) { // item index  
    this.categoryPetitionPageIndex = pageIndex;
  }

  @action
  async getPetitionFeed (page, limit, type) { //    Repository   
    try {
      const response = await petitionRepository.getPetitionFeed(page, limit, type);
      
      this.allowedPetitions = response.data.petition; //   observable  
      this.allowedPetitionTotalPage = response.data.totalPage; //   observable  

      return new Promise((resolve, reject) => {// resonse  
        resolve(response);
      });
    } catch (error) {
      console.error(error);
      
      return new Promise((resolve, reject) => {//  catch
        reject(error);
      });
    }
  }

  @action
  async getPetitionFeedByCategory (page, limit, category) { //    Repository   
    try {
      const response = await petitionRepository.getPetitionFeedByCategory(page, limit, category);

      this.categoryPetitions = response.data.petition; //   observable  
      this.categoryPetitionTotalPage = response.data.totalPage; //   observable  

      return new Promise((resolve, reject) => { // resonse  
        resolve(response);
      });
    } catch (error) {
      console.error(error);
      
      return new Promise((resolve, reject) => { //  catch
        reject(error);
      });
    }
  }

  @action
  async searchPetition (title, page, limit) { //    Repository   
    try {
      const response = await petitionRepository.searchPetition(title, page, limit);

      this.allowedPetitions = response.data.petition; //   observable  
      this.allowedPetitionTotalPage = response.data.totalPage; //   observable  

      return new Promise((resolve, reject) => { // resonse  
        resolve(response);
      });
    } catch (error) {
      console.error(error);
      
      return new Promise((resolve, reject) => { //  catch
        reject(error);
      });
    }
  }

  @action
  async searchCategoryPetition (title, page, limit) { //    Repository   
    try {
      const response = await petitionRepository.searchPetition(title, page, limit);

      this.categoryPetitions = response.data.petition; //   observable  
      this.categoryPetitionTotalPage = response.data.totalPage; //   observable  

      return new Promise((resolve, reject) => { // resonse  
        resolve(response);
      });
    } catch (error) {
      console.error(error);
      
      return new Promise((resolve, reject) => { //  catch
        reject(error);
      });
    }
  }

  @action
  async getPetitionDetail (idx) { //      Repository   
    try {
      const response = await petitionRepository.getPetitionDetail(idx);

      this.PetitionDetailData = response.data.petition;

      return new Promise((resolve, reject) => { // resonse  
        resolve(response);
      });
    } catch (error) {
      console.error(error);
      
      return new Promise((resolve, reject) => { //  catch
        reject(error);
      });
    }
  }

  
  @action
  async deletePetition (idx) { //      Repository   
    try {
      const response = await petitionRepository.deletePetition(idx);

      return new Promise((resolve, reject) => { // resonse  
        resolve(response);
      });
    } catch (error) {
      console.error(error);
      
      return new Promise((resolve, reject) => { //  catch
        reject(error);
      });
    }
  }

  @action
  async blindPetition (request) { //      Repository   
    try {
      const response = await petitionRepository.blindPetition(request);

      return new Promise((resolve, reject) => { // resonse  
        resolve(response);
      });
    } catch (error) {
      console.error(error);
      
      return new Promise((resolve, reject) => { //  catch
        reject(error);
      });
    }
  }

  async answerToPetition (request) { //      Repository   
    try {
      const response = await petitionRepository.answerToPetition(request);

      return new Promise((resolve, reject) => { // resonse  
        resolve(response);
      });
    } catch (error) {
      console.error(error);
      
      return new Promise((resolve, reject) => { //  catch
        reject(error);
      });
    }
  }

  async updatePetitionAnswer (request) { //      Repository   
    try {
      const response = await petitionRepository.updatePetitionAnswer(request);

      return new Promise((resolve, reject) => { // resonse  
        resolve(response);
      });
    } catch (error) {
      console.error(error);
      
      return new Promise((resolve, reject) => { //  catch
        reject(error);
      });
    }
  }

  @action
  async getStudentCouncilPetition (page, limit, type) { //      Repository   
    try {
      const response = await petitionRepository.getStudentCouncilPetition(page, limit, type);

      this.studentCouncilPetitions = response.data.petition;
      this.studentCouncilPetitionsTotalpage = response.data.totalPage;

      return new Promise((resolve, reject) => { // resonse  
        resolve(response);
      });
    } catch (error) {
      console.error(error);
      
      return new Promise((resolve, reject) => { //  catch
        reject(error);
      });
    }
  }

  @action
  async writePetition (request) { //  
    try {
      const response = await petitionRepository.writePetition(request);

      return new Promise((resolve, reject) => { // resonse  
        resolve(response);
      });
    } catch (error) {
      console.error(error);
      
      return new Promise((resolve, reject) => { //  catch
        reject(error);
      });
    }
  }

  @action
  async writePetitionComment (request) { //   
    try {
      const response = await petitionRepository.writePetitionComment(request);

      return new Promise((resolve, reject) => { // resonse  
        resolve(response);
      });
    } catch (error) {
      console.error(error);
      
      return new Promise((resolve, reject) => { //  catch
        reject(error);
      });
    }
  }

  @action
  async getPetitionComments (idx) { //   
    try {
      const response = await petitionRepository.getPetitionComments(idx);

      this.petitionComment = response.data.comment;

      return new Promise((resolve, reject) => { // resonse  
        resolve(response);
      });
    } catch (error) {
      console.error(error);
      
      return new Promise((resolve, reject) => { //  catch
        reject(error);
      });
    }
  }
}

export default petitionStore;