import React, { useEffect, useState } from 'react';
import PropTypes  from 'prop-types';
import classNames from 'classnames/bind';
import style from './PetitionTemplate.scss';
import { FiSearch } from 'react-icons/fi';

const cx = classNames.bind(style);

const PetitionTemplate = ({ 
    allowedPetitionsItem, 
    categoryPetitionsItem, 
    allowedPetitionIndexItemList, 
    categoryPetitionsIndexItemList, 
    category,
    handleCategory,
    handlePetitionType,
    type,
    handleSearchPetition,
    handleSearchCategoryPetition
  }) => {
    
  if (category === '') {
    category = '';
  };

  switch (type) {
    case 'allowed':
      type = '';
      break;
    case 'not_allowed':
      type = '  ';
      break;
    case 'order':
      type = ' ';
      break;
    case 'vote_order':
      type = ' ';
      break;
    default:
      break;
  }

  const [title, setTitle] = useState('');
  const [categoryTitle, setCategoryTitle] = useState('');

  const handleKeypressForTypePetition = (e) => {
    if (e.key === 'Enter') {
      handleSearchPetition(title);
    }
  };

  const handleKeyPressForCategory = (e) => {
    if (e.key === 'Enter') {
      handleSearchCategoryPetition(categoryTitle);
    }
  };

  return (
    <>
      <div className={cx('isAllowedSelectDiv')}>
        <div className={cx('isAllowedSelectDiv-allowedSelectDiv', { 'isAllowedSelectDiv-allowedSelectDiv-style': type === ' ' })} onClick={() => handlePetitionType('order')}>
             
        </div>
        <div className={cx('isAllowedSelectDiv-notAllowedSelectDiv', { 'isAllowedSelectDiv-notAllowedSelectDiv-style': type === '  ' })}  onClick={() => handlePetitionType('not_allowed')}>
              
        </div>
        <div className={cx('isAllowedSelectDiv-allowedSelectDiv', { 'isAllowedSelectDiv-orderPetitionSelectDiv-style': type === '' })}  onClick={() => handlePetitionType('allowed')}>
            
        </div>
        <div className={cx('isAllowedSelectDiv-orderPetitionSelectDiv', { 'isAllowedSelectDiv-orderPetitionSelectDiv-style': type === ' ' })}  onClick={() => handlePetitionType('vote_order')}>
             
        </div>  
      </div>
      <div className={cx('PetitionTemplate')}>
        <div className={cx('PetitionTemplate-header')}>
          <div className={cx('PetitionTemplate-header-title')}>
            <span>{type}  </span>
            <div className={cx('PetitionTemplate-header-title-searchDiv')}>
              {
                type === ' ' ? 
                <>
                    <span> :</span>
                    <input className={cx('PetitionTemplate-header-title-searchDiv-searchInput')} placeholder={' '} value={title} onChange={(e) => { handleSearchPetition(e.target.value); setTitle(e.target.value)}} onKeyPress={(e) => handleKeypressForTypePetition(e)}/>
                    <div className={cx('PetitionTemplate-header-title-searchDiv-iconDiv')} onClick={() => handleSearchPetition(title)}>
                      <FiSearch className={cx('PetitionTemplate-header-title-searchDiv-iconDiv-icon')}/>
                    </div>
                </>
                : <></>
              }
            </div>
          </div>
          <div className={cx('PetitionTemplate-header-kinds')}>
            <div className={cx('PetitionTemplate-header-kinds-category')}>
              <span></span>
            </div>
            <div className={cx('PetitionTemplate-header-kinds-title')}>
              <span></span>
            </div>
            <div className={cx('PetitionTemplate-header-kinds-date')}>
              <span> </span>
            </div>
            <div className={cx('PetitionTemplate-header-kinds-voteCount')}>
              <span> </span>
            </div>
          </div>
        </div>
        {
          allowedPetitionsItem.length !== 0 ?
          allowedPetitionsItem
          : allowedPetitionsItem.length === 0 ?
            <div className={cx('notFoundPeition')}>
                .
            </div>
            :<></>
        }
      </div>
      <div className={cx('PetitionIndexItemTemplate')}>
        {allowedPetitionIndexItemList}
      </div>
      <div className={cx('CategoryTitile')}>
        <span>*   </span>
      </div>
      <div className={cx('Category')}>
        <div className={cx('Category-CategoryRowOne')}>
          <div className={cx('Category-CategoryRowOne-contentsDiv', {'Category-CategoryRowOne-contentsDiv-contentsStyle': category === ''})} onClick={() => handleCategory('')}>
            <span></span>
          </div>
          <div className={cx('Category-CategoryRowOne-contentsDiv',  {'Category-CategoryRowOne-contentsDiv-contentsStyle': category === ''})} onClick={() => handleCategory('')}>
            <span></span>
          </div>
          <div className={cx('Category-CategoryRowOne-contentsDiv' ,  {'Category-CategoryRowOne-contentsDiv-contentsStyle': category === ''})}  onClick={() => handleCategory('')}>
            <span></span>
          </div>
          <div className={cx('Category-CategoryRowOne-contentsDiv',  {'Category-CategoryRowOne-contentsDiv-contentsStyle': category === ''})}  onClick={() => handleCategory('')}>
            <span></span>
          </div>
          <div className={cx('Category-CategoryRowOne-contentsDiv',  {'Category-CategoryRowOne-contentsDiv-contentsStyle': category === ''})}  onClick={() => handleCategory('')}>
            <span></span>
          </div>
        </div>
        <div className={cx('Category-CategoryRowTwo')}>
          <div className={cx('Category-CategoryRowTwo-contentsDiv',  {'Category-CategoryRowTwo-contentsDiv-contentsStyle': category === ''})}  onClick={() => handleCategory('')}>
            <span></span>
          </div>
          <div className={cx('Category-CategoryRowTwo-contentsDiv',  {'Category-CategoryRowTwo-contentsDiv-contentsStyle': category === ''})}  onClick={() => handleCategory('')}>
            <span></span>
          </div>
          <div className={cx('Category-CategoryRowTwo-contentsDiv',  {'Category-CategoryRowTwo-contentsDiv-contentsStyle': category === ''})}  onClick={() => handleCategory('')}>
            <span></span>
          </div>
          <div className={cx('Category-CategoryRowTwo-contentsDiv',  {'Category-CategoryRowTwo-contentsDiv-contentsStyle': category === ''})}  onClick={() => handleCategory('')}>
            <span></span>
          </div>
          <div className={cx('Category-CategoryRowTwo-contentsDiv',  {'Category-CategoryRowTwo-contentsDiv-contentsStyle': category === ''})}  onClick={() => handleCategory('')}>
            <span></span>
          </div>
        </div>
      </div>
      <div className={cx('PetitionTemplate')}>
        <div className={cx('PetitionTemplate-header')}>
          <div className={cx('PetitionTemplate-header-title')}>
            <span>{category}  </span>
            <div className={cx('PetitionTemplate-header-title-searchDiv')}>
            {
                category === '' ? 
                <>
                    <span> :</span>
                    <input className={cx('PetitionTemplate-header-title-searchDiv-searchInput')} placeholder={' '} value={categoryTitle} onChange={(e) => { handleSearchCategoryPetition(e.target.value); setCategoryTitle(e.target.value)}} onKeyPress={(e) => handleKeyPressForCategory(e)}/>
                    <div className={cx('PetitionTemplate-header-title-searchDiv-iconDiv')} onClick={() => handleSearchCategoryPetition(categoryTitle)}>
                      <FiSearch className={cx('PetitionTemplate-header-title-searchDiv-iconDiv-icon')}/>
                    </div>
                </>
                : <></>
              }
            </div>
          </div>
        </div>
        <div className={cx('PetitionTemplate-header-kinds')}>
            <div className={cx('PetitionTemplate-header-kinds-category')}>
              <span></span>
            </div>
            <div className={cx('PetitionTemplate-header-kinds-title')}>
            <span></span>
            </div>
            <div className={cx('PetitionTemplate-header-kinds-date')}>
            <span> </span>
            </div>
            <div className={cx('PetitionTemplate-header-kinds-voteCount')}>
            <span> </span>
            </div>
        </div>
        {
          categoryPetitionsItem.length !== 0 ?
            categoryPetitionsItem
            : categoryPetitionsItem.length === 0 ?
              <div className={cx('notFoundPeition')}>
                  .
              </div>
              :<></>
        }
      </div>
      <div className={cx('PetitionIndexItemTemplate')}>
        {categoryPetitionsIndexItemList}
      </div>
    </>
  );
};

PetitionTemplate.propTypes = {
  allowedPetitionsItem: PropTypes.array,
  allPetitionsItem: PropTypes.array
};

export default PetitionTemplate;